.post-stat {
  @include body-2;

  display: flex;
  align-items: center;
  line-height: 16px;
  color: map-get($font-colors, secondary);

  &__item {
    display: flex;
    align-items: center;

    & + & {
      margin-left: 24px;
    }
  }

  &__label {
    margin-left: 8px;
  }
}
