.label {
  @include labels-text;
  padding: 4px 8px;
  color: map-get($font-colors, base);
  background-color: map-get($colors, highlighter);
  border-radius: $border-radius;

  & + & {
    margin-left: 8px;
  }

  &--accent {
    color: map-get($font-colors, base-light);
    background-color: map-get($colors, accent);
  }
}
