.input {
  @include body-1(false);
  box-sizing: border-box;
  padding-left: 16px;
  width: 100%;
  height: 56px;
  font: inherit;
  color: inherit;
  -webkit-appearance: none;
  background-color: map-get($colors, neutral);
  border: none;
  border-radius: $border-radius;
  box-shadow: map-get($shadows, button);
  transition: map-get($transition, medium);

  &::placeholder {
    color: map-get($font-colors, inactive);
  }

  &:focus {
    background-color: darken(map-get($colors, neutral), 5);
  }

  @include respond-to(medium) {
    height: 64px;
    line-height: 64;
  }
}
